(function() {
    'use strict';
    angular.module('app').controller('DbGaugeController', DbGaugeController); // for testing
    angular.module('app').directive('dbGauge', function() {
      return {
        link: DbGaugeLink,
        controllerAs: 'vm',
        restrict: 'A',
        scope: {},
        controller: DbGaugeController,
      }
    });
  
    
    function DbGaugeController($scope, AUDIO_CFG, audioState, Canvas2DHandler) {
      var ctrl = this;

      this.AUDIO_CFG = AUDIO_CFG;
      this.audioState = audioState;
      this.c2dh = Canvas2DHandler;
      this.canvasWidth = null;
      this.canvasHeight = null;
      this.volumePosRange = null;
      this.clipLinePosPct = 0.90;
      this.clipColor = 'red';
      this.volumeColor = '#3ae024';

      $scope.$on('updateDbGauge', function(e, data) {
        audioState.volumePct = Math.min(1.5, audioState.signal / AUDIO_CFG.SIGNAL_RANGE.clip); // 150% is max clip range

        if(audioState.signal >= AUDIO_CFG.SIGNAL_RANGE.clip) {
          audioState.clipping = true;
          audioState.clipPct = (audioState.volumePct - 1) * 2; // 50% actual = 100% displayed
        }

        else {
          audioState.clipping = false;
        }
      });

      this.onCanvasSizeChange = function() {
        ctrl.canvasWidth = ctrl.c2dh.canvas.width;
        ctrl.canvasHeight = ctrl.c2dh.canvas.height;
        ctrl.volumePosRange = {
          min: 0,
          max: ctrl.clipLinePosPct*ctrl.canvasWidth,
        };
      }

      this.init = function(el) {
        if(!(el instanceof HTMLCanvasElement)) {
          throw 'InvalidCanvasError';
        }

        var canvas = el;
        
        canvas.height = 40;
        ctrl.c2dh.setContext(canvas.getContext('2d'));
        ctrl.onCanvasSizeChange(); // init these values
        ctrl.draw(); // start draw cycle
      }

      this.draw = function(timeStamp) {
        ctrl.c2dh.beginDraw();

        // clip line
        ctrl.c2dh.drawLine(
          ctrl.volumePosRange.max, 0, 
          ctrl.volumePosRange.max, ctrl.canvasHeight,
          ctrl.clipColor
        );

        // volume bar
        if(ctrl.audioState.volumePct > 0) {
          var clampedVolumePct = Math.min(1, ctrl.audioState.volumePct);

          ctrl.c2dh.drawRect(
            ctrl.volumePosRange.min, 0, 
            clampedVolumePct*ctrl.volumePosRange.max, ctrl.canvasHeight, 
            ctrl.volumeColor);
        }

        // clip bar
        if(ctrl.audioState.clipping) {
          ctrl.c2dh.drawRect(
            ctrl.volumePosRange.max, 0, 
            ctrl.audioState.clipPct*ctrl.canvasWidth, ctrl.canvasHeight,
            ctrl.clipColor
          );
        }

        ctrl.c2dh.endDraw(ctrl.draw);
      }
    }

    function DbGaugeLink($scope, $el, $attrs, $ctrl) {
      try {
        $ctrl.init($el[0]);
      }
      catch(e) {
        console.error(e);
      }
    }
  
})();

(function() {
    'use strict';
  
    angular.module('app').component('referencePitchMonitor', {
      controller: ReferencePitchMonitorController,
      controllerAs: 'vm',
      templateUrl: 'app/components/reference-pitch-monitor/reference-pitch-monitor.template.html',
    });
  
    
    function ReferencePitchMonitorController($scope, AUDIO_CFG, AudioMath, audioState, pitchDetect) {
      var ctrl = this;

      $scope.audioState = audioState; // isolated scope
      this.prevInputFreq = 0;

      $scope.$on('updateRpm', function(e, data) {
        var inputFreq = audioState.signal;
        var _default = false;
        
        if(inputFreq >= AUDIO_CFG.SIGNAL_RANGE.min) {

            var pitch = pitchDetect.autoCorrelate(data.samples, AUDIO_CFG.SAMPLE_RATE);

            if(!isNaN(pitch) && pitch > -1) {
              if(!isNaN(audioState.refPitch.noteNum) && !audioState.refPitch.shouldInit) {
                audioState.pitchDelta = AudioMath.calculatePitchDelta(
                  pitchDetect.frequencyFromNoteNumber(audioState.refPitch.noteNum),
                  pitch).toFixed(1);
              }
              
              if(
                audioState.refPitch.shouldInit || 
                isNaN(audioState.refPitch.noteNum) || 
                !audioState.refPitch.note || 
                Math.abs(audioState.pitchDelta) >= AUDIO_CFG.PITCH_DELTA_ABSOLUTE_THRESHOLD) {
  
                  audioState.refPitch.noteNum = pitchDetect.noteFromPitch(pitch);
                  audioState.refPitch.note = (!isNaN(audioState.refPitch.noteNum) && audioState.refPitch.noteNum > -1) ? AUDIO_CFG.TONE_NAMES[(audioState.refPitch.noteNum % 12)+1] : AUDIO_CFG.TONE_NAMES[0];
                  audioState.refPitch.shouldInit = false;
              }
  
              audioState.pitch = pitch;
              ctrl.prevInputFreq = inputFreq;
            }

            else _default = true;

        }

        else _default = true;


        if(_default) {
          audioState.refPitch.note = AUDIO_CFG.TONE_NAMES[0];
          audioState.refPitch.noteNum = NaN;
          audioState.pitchDelta = 0;
        }


        $scope.$digest();

      });
    }
  
})();

(function() {
    'use strict';
  
    angular.module('app').component('appRoot', {
      controller: AppController,
      controllerAs: 'vm',
      templateUrl: 'app/components/app/app.template.html',
    });
  
    
    function AppController($scope, getUserMedia, AUDIO_CFG, audioContext, AudioMath, audioState, Alerts) {

      var ctrl = this;
      ctrl.alerts = Alerts;
      ctrl.viewAlerts = {
        enableMicAlert: null,
        notFoundAlert: null,
        notSupportedAlert: null,
        notAllowedAlert: null,
        unknownAlert: null,
      };

      var prevAudioSignal = null;

      ctrl.initAlerts = function() {
        ctrl.viewAlerts.enableMicAlert = Alerts.addAlert('primary', 'inline-partials/enable-mic-alert.html', true, false);
        ctrl.viewAlerts.notFoundAlert = Alerts.addAlert('danger', 'inline-partials/not-found-alert.html');
        ctrl.viewAlerts.notSupportedAlert = Alerts.addAlert('danger', 'inline-partials/not-supported-alert.html');
        ctrl.viewAlerts.notAllowedAlert = Alerts.addAlert('danger', 'inline-partials/not-allowed-alert.html');
        ctrl.viewAlerts.unknownAlert = Alerts.addAlert('danger', 'inline-partials/unknown-alert.html');
      }

      ctrl.gUMSuccess = function(stream) {
        try {
          var mediaSourceState = audioState.mediaSource;
          mediaSourceState.stream = audioContext.createMediaStreamSource(stream);

          // using only 1 processor to make sure all gauges are reading from the exact same data
          mediaSourceState.appProcessor = audioContext.createScriptProcessor(AUDIO_CFG.FFT_SIZE, 2, 1);
          mediaSourceState.appProcessor.onaudioprocess = ctrl.onAudioProcess;
  
          mediaSourceState.stream.connect(mediaSourceState.appProcessor);
          mediaSourceState.appProcessor.connect(audioContext.destination);
  
          ctrl.viewAlerts.enableMicAlert.close();
          $scope.$digest();
        }

        catch(e) {
          console.error(e);
          ctrl.viewAlerts.unknownAlert.open();
        }
      };

      ctrl.gUMError = function(err) {
        switch(err.name) {
          case 'NotFoundError':
            ctrl.viewAlerts.notFoundAlert.open();
            break;

          case 'NotAllowedError':
            ctrl.viewAlerts.notAllowedAlert.open();
            break;

          default:
            console.error(err);
            ctrl.viewAlerts.unknownAlert.open();
            break;
        }

        $scope.$digest();
      };

      ctrl.$onInit = function() {
        ctrl.initAlerts();
      };

      // istanbul ignore next
      ctrl.selectInput = function(e) {
        if(!getUserMedia) {
          ctrl.viewAlerts.notSupportedAlert.open();
          return;
        }

        getUserMedia.call(
          navigator, 
          {
            audio: {
              mandatory: {
                googEchoCancellation: false,
                googAutoGainControl: false,
                googNoiseSuppression: false,
                googHighpassFilter: false,
              }
            }
          },
        ctrl.gUMSuccess,
        ctrl.gUMError);
        // .then(ctrl.gUMSuccess)
        // .catch(ctrl.gUMError);
      };

      // tested onAudioProcess with a mock.
      // istanbul ignore next
      ctrl.onAudioProcess = function(e) {
        var samples = e.inputBuffer.getChannelData(0);
        var avgSignal = AudioMath.calculateAverageSignal(samples, AUDIO_CFG.SAMPLE_RATE, AUDIO_CFG.NFFT_SIZE);
        avgSignal = Math.min(avgSignal, AUDIO_CFG.SIGNAL_RANGE.max);


        // smooth
        if(prevAudioSignal) {
          audioState.signal = Math.max(avgSignal, prevAudioSignal*AUDIO_CFG.SMOOTHING_CONSTANT);
        }
        else {
          audioState.signal = avgSignal;
        }

        $scope.$broadcast('updateDbGauge'); // need to update db gauge before anything else
        $scope.$broadcast('updateRpm', { samples: samples });

        prevAudioSignal = audioState.signal;
      };
      
    }
  
})();

(function() {
    'use strict';

    angular.module('lib').service('Canvas2DHandler', Canvas2DHandler);

    
    function Canvas2DHandler(_, requestAnimationFrame) {
        var _ctx = null;
        this._rafId = null;
        this.canvas = null;

        this.setContext = function(ctx) {
            if(ctx instanceof CanvasRenderingContext2D) {
                _ctx = ctx;
                this.canvas = _ctx.canvas;
            }
        }

        this.getContext = function() {
            if(_ctx instanceof CanvasRenderingContext2D)
                return Object.create(_ctx);
            else
                return null;
        }

        this.clear = function() {
            _ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
        }

        this.drawRect = function(x, y, w, h, s) {
            if(_ctx) {
                _ctx.fillStyle = s;
                _ctx.fillRect(x, y, w, h);
            }
        }

        this.drawLine = function(x1, y1, x2, y2, s) {
            if(_ctx) {
                _ctx.beginPath();
                _ctx.moveTo(x1, y1);
                _ctx.lineTo(x2, y2);
                _ctx.strokeStyle = s || 'black';
                _ctx.stroke();
                _ctx.closePath();
            }
        }

        this.beginDraw = function() {
            this.clear();
        }

        this.endDraw = function(rafCb) {
            this._rafId = requestAnimationFrame(rafCb || function() {});
        }
    }
    
})();

(function() {
    'use strict';

    angular.module('lib').service('Alerts', AlertsService);

    
    function AlertsService(_) {
        var srvc = this;
        srvc.data = [];

        srvc.addAlert = function(type, templateUrl, open, closeable, additionalClasses) {
            /* istanbul ignore next */
            open = (open !== undefined) ? open : false;
            /* istanbul ignore next */
            closeable = (closeable !== undefined) ? closeable : true;
            /* istanbul ignore next */
            additionalClasses = (additionalClasses !== undefined) ? additionalClasses : '';
            
            var alert = {
                id: this.data.length,
                type: type,
                show: open,
                templateUrl: templateUrl,
                additionalClasses: additionalClasses,
                closeable: closeable,
            };

            this.data.push(alert);

            var alertHandle = {
                open: function() {
                    alert.show = true;
                },

                close: function() {
                    alert.show = false;
                },

                remove: function() {
                    srvc.removeAlert(alert.id);
                },
            };

            return alertHandle;
        }

        srvc.removeAlert = function(id) {
            _.forEach(this.data, function(alert, i) {
                /* istanbul ignore else */
                if(alert.id === id) {
                    srvc.data.splice(i, 1);
                    return false;
                }
            });
        };

        srvc.openAlert = function(id) {
            _.forEach(this.data, function(alert) {
                /* istanbul ignore else */
                if(alert.id === id) {
                    alert.show = true;
                    return false;
                }
            });
        };

        srvc.closeAlert = function(id) {
            _.forEach(this.data, function(alert) {
                /* istanbul ignore else */
                if(alert.id === id) {
                    alert.show = false;
                    return false;
                }
            });
        };

        srvc.openAll = function() {
            _.map(this.data, function(alert) {
                alert.show = true;
            });
        };

        srvc.closeAll = function() {
            _.map(this.data, function(alert) {
                alert.show = false;
            });
        };

        srvc.removeAll = function() {
            this.data = [];
        };
    }
    
})();

